.nft-jacker {
    font-family: stem;
    background-color: black;
    height: 100%;
    padding: 16%;
    display: flex;
    flex-direction: column;
    z-index: 1;

    h1 {
        font-size: 46pt;
    }

    .back-btn {
        position: absolute;
        left: 25px;
        top: 27px;
        cursor: pointer;
        width: 30px;
        height: 30px;
        background-size: contain;
        background-repeat: no-repeat;
        z-index: 1;
    }

    .error-message {
        margin-left: auto;
        margin-right: auto;
        color: red;
        max-width: 50%;
    }

    .player {
        pointer-events: none;
        opacity: 0.3;
        filter: blur(9px);
    }

    .loaded.player {
        pointer-events: unset;
        opacity: unset;
        filter: unset;
    }
    .artist-name {
        padding: 10px;
        position: absolute;
        color: #595959;
        top: 20px;
        right: 20px;
        font-size: 20pt;
        text-align: right;
        z-index: 1;

        a.current-link {
            color: white;
            font-size: 24pt;
            display: flex;

            .source {
                font-size: 14pt;
                margin-right: 10px;
                line-height: 44px;
            }
        }

        .cover-art.smaller {
            width: 128px;
        }

        .cover-art {
            margin-top: 30px;
            width: 165px;
            border-radius: 8px;
            border: 1px solid #877c7c;
        }
        
        .song-name {
            color: white;
            font-size: 14pt;
            max-width: 300px;
            margin-left: auto;
        }

        .description {
            margin-top: 20px;
            max-width: 300px;
            color: white;
            font-size: 10pt;
            text-align: left;

        }
    }

    h1 {
        text-align: center;
        //color: #61fc9f;
        color: transparent;
        z-index: 1;
    }

    .player-container.show {
        margin: auto;
        left: 0px;
        top: 0px;
        bottom: 0px;
        right: 0px;
        position: absolute;

        iframe.keyon-player {
            border-color: transparent !important;
            box-shadow: rgb(87 84 84 / 40%) 0px 12px 28px 0px, rgb(0 0 0 / 35%) 0px 2px 4px 0px, rgb(0 0 0 / 16%) 0px 0px 0px 1px inset;
        }

        iframe {
            margin: auto;
            position: absolute;
            left: 0px;
            top: 0px;
            right: 0px;
            bottom: 0px;
            box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px !important;
        }
    }
    .player-container.hide {
        display: none;
    }
    
    .nft-link.hide {
        display: none;
    }

    iframe {
        border-radius: 30px;
    }
    .nft-link {
        .clear-btn {
            position: absolute;
            left: 390px;
            top: 38px;
            cursor: pointer;
        }

        // margin-top: 5%;
        padding-left: 20px;
        // margin-right: auto;
        width: 100%;
        padding-bottom: 4px;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        height: 135px;
        left: 0;
        top: 0;
        padding-top: 30px;
        z-index: 10000;
        background-color: #00000075;
        backdrop-filter: blur(8px);
        position: absolute;

        input {
            width: 400px;
            color: #61fc9f;
            background-color: #131415;
            border-radius: 8px;
            outline: none !important;
            border: 1px solid #ffffff4a;
            padding: 10px;
        }

        .jack-btn {
            position: relative;
            padding: 9px;
            background-color: white;
            color: black;
            border: 10px;
            border-radius: 15px;
            text-align: center;
            align-items: flex-start;
            font-size: 14pt;
            font-weight: 600;
            width: 140px;
            padding-left: 20px;
            margin-left: 15px;
            // margin-right: auto;
            // margin-top: 5%;
            cursor: pointer;
        }

        .jack-btn:hover {
            filter: brightness(.9);
        }
        .jack-btn:before {
            position: absolute;
            top: 50%;
            left: 22px;
            width: 24px;
            height: 25px;
            content: "";
            transform: translateY(-50%);
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5 12.5h14M12 5.5l7 7-7 7' stroke='%23000' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
        }
    }

    .helper-icon {
        position: absolute;
        bottom: 10px;
        left: 26px;
        font-size: 42pt;
        color: white;
        z-index: 2;
        cursor: pointer;
    }

}

.jacked.nft-jacker {

    .player-helper {
        .mock-player {
            transform: translate(0px, -8%);
        }

        .x-button {
            position: absolute;
            right: 25px;
            top: 25px;
            font-size: 32pt;
        }
    }
}

@keyframes slide-artist {
    from {
        top: 25px;
    }

    to {
        top: 100px;
    }
}

@keyframes slide-test-artist {
    from {
        margin-top: 0px;
    }

    to {
        margin-top: 100px;
    }
}


