    // background-image: url("data:image/svg+xml,%3Csvg width='24' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5 12.5h14M12 5.5l7 7-7 7' stroke='%23000' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
 
}

.password-protected {
    background-color: black;
    height: 100%;
    display: flex;
    font-family: 'DecimaMonoCyr';

    .spore-future {
        margin: auto;
    }
    
    .spore-future-logo {
        width: 30vh;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 5vh;
    }

    .password-box {
        margin: auto;
        width: 300px;
        height: 300px;
        display: flex;
        flex-direction: column;

        input {
            background-color: #0000004a;
            outline: none !important;
            border: none;
            border-bottom: 1px solid white;
            color: white;
        }

        .password-enter {
            margin-top: 5px;
            background-color: white;
