@media screen and (max-width: 600px) {
    .stockLeft {
        top: unset;
        left: 15px !important;
        right: unset !important;
        bottom: 10px !important;
        margin: unset !important;
    }

    .show-remixes {
        bottom: 25px;
    }

    .remix-list-container {
        width: 95% !important;
        top: 15px;
        border-radius: 25px;
        height: 100% !important;
        overflow: scroll;
        z-index: 100000000 !important;
    }

    .nft-jacker.jacked {
        .share-button {
            top: unset;
            margin: unset;
            bottom: 10px;
            left: 10px;
            font-size: 20px;
        }
        .share-button-modal {
            width: 96%;

            .share-url-field {
                padding: 25px;
                p {
                    overflow-wrap: anywhere;
                    margin-right: 10px;
                }
            }
        }
        .timer {
            display: none;
        }
        .helper-icon {
            font-size: 32pt;
            display: none;
        }
        .helper-icon {
            bottom: 10px !important;

        }
        .stockLeft {
            top: unset !important;
            left: 0px !important;
            right: 0px !important;
            bottom: 10px !important;
            margin: auto !important;
            font-size: 10pt;
        }
        .nft-link {
            .jack-btn {
                margin-top: 5px;
                color: black;
                width: 60px;
            }

            .clear-btn {
                left: 295px;
            }
        }

        a.current-link {
            position: fixed;
            left: 0px;
            top: 120px;
        }
    }
    .nft-jacker.showing-link-input {
        .player-container.show {
            z-index: 10000;
        }

        .artist-name {
            .cover-art {
                animation-duration: 1s;
                margin-top: 100px;
                animation-name: slide-test-artist; 
            }
        }
        .back-btn {
            opacity: 0.1;
            margin-top: 100px;
            animation-duration: 1s;
            animation-name: slide-test-artist; 
        }
        .artist-name {
            opacity: 0.1;
            top: 100px;
            animation-duration: 1s;
            animation-name: slide-artist; 
        }
    }

    .nft-jacker {

        .player-container.show {
            position: fixed;
        }

        .back-btn {
            top: 20px;
            right: 20px;
            width: 30px;
            left: unset;
        }

        .back-btn:before {
            top: 10px;
            left: 10px;
            transform: translateY(-50%) rotate(180deg) scale(1.2);
        }


        .artist-name {
            top: 0px;
            position: fixed;
            font-size: 14pt;
            right: unset;
            text-align: left;
            left: 100px;

            .song-name {
                font-size: 11pt;
                margin-top: 6px;
                max-width: 230px;
                line-height: 18px;
            }
            .cover-art, .cover-art.smaller {
                border-radius: 0px;
                margin: 0px;
                position: fixed;
                top: 10px;
                left: 10px;
                width: 90px;
            }
        }
        .nft-link {
            // margin-top: 20%;
            align-items: flex-start;
            width: 100% !important;
            //font-size: 12pt;
            height: 108px;
            left: 0px !important;
            padding-left: 10px;
            padding-top: 10px;

            .clear-btn {
                left: 305px;
                top: 19px;
                z-index: 100000;
            }

            .jack-btn {
                padding: 5px;
                font-size: 0px;
                width: 40px;
                height: 30px;
                margin-right: 15px;
                margin-left: 15px;
                margin-top: 5px;
            }

            .jack-btn::before {
                //background-image: unset;
                left: 5px;
            }
        }

        .player-container.show {
            transform: translate(0px, 25px);
        }
    }
}


@media screen and (max-width: 600px) {
    #root, body, html {
        overflow: hidden !important;
    }
}
