.locked-notification-background {
    background-color: #0000004a;
    backdrop-filter: blur(8px);
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: flex;

    .loaded-shader {
        opacity: .3;
        animation-name: fade-in-shader;
        animation-duration: 3s;
    }
    iframe {
        pointer-events: none;
        border-radius: 0px !important;
        border: none !important;
        position: absolute;
        width: 100% !important;
        height: 100% !important;
        left: 0px;
        top: 0px;
        opacity: 0;
        
    }

    .locked-notification {
        width: 330px;
        z-index: 3;
        height: 260px;
        background-color: #5d565936;
        background-image: linear-gradient(240deg, #000000ad, #1414148a);
        border: 2px solid #cacaca;
        margin: auto;
        color: white;
        padding: 15px;
        border-radius: 15px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .connect-button-wrapper {
            position: unset;
            margin: auto;
            bottom: unset !important;
            right: unset !important;
            transform-origin: center;
        }

        .logo-container {
            display: flex;
            width: 100%;
            margin-bottom: 15px;
            img {
                width: 30px;
                height: 30px;
                margin-left: auto;
                margin-right: auto;
            }
        }
        
        .go-to-buy {
            margin-left: auto;
            margin-right: auto;
            margin-top: 30px;
            align-items: flex-start;
            background-color: black;
            color: white;
            padding: 8px;
            border-radius: 10px;
            padding-left: 14px;
            padding-right: 14px;

            span.buy-spore-players-text {
                font-size: 12pt;
                height: unset;
                padding: 0px;
                padding-left: 14px;
                padding-right: 14px;
            }
        }

        .already-own-one {
            margin-top: 15px;
            border-bottom: 1px solid white;
            cursor: pointer;
            padding-bottom: 1px;
            margin-left: auto;
            margin-right: auto;
        }
    }
}

@keyframes fade-in-shader {
    from {
        opacity: 0;
    }

    to {
        opacity: .3;
    }
}
