.zorb-animation {
    position: absolute;
    pointer-events: none;
    width: 50px;
    height: 50px;
    z-index: 1000000000000;
    animation-name: zorb-anim;
    animation-duration: 1s;
}

@keyframes zorb-anim {
    0% {
        opacity: 1;
        left: var(--zorb-start-x);
        top: var(--zorb-start-y);
    }

    50% {
        opacity: 1;
        left: var(--zorb-mid-x);
        top: var(--zorb-mid-y);
    }

    80% {
        opacity: 1;
        left: var(--zorb-end-x);
        top: var(--zorb-end-y);
    }

    100% {
        opacity: 0;
        left: var(--zorb-end-x);
        top: var(--zorb-end-y);
    }
}
