.nft-jacker.jacked {

    .shuffle-buttons {
        display: flex;
        flex-direction: column;
        z-index: 3;
        position: relative;

        .song-waveform {
            position: absolute;
            top: 42px;
            right: 8px;
        }

        .play-and-slider {
            margin-top: auto;
            display: flex;
            align-items: center;
        }

       .play-btn-wrapper {
            z-index: 1000000;
            margin-right: 0px;
            margin-top: unset;
            width: 46px;
            height: 50px;
            padding: 12px;
            padding-top: 12px;
            border: none !important;
            background-color: transparent !important;
            display: flex;
            align-items: center;
        }

        .slider-container {
            --d:330px;
            --angle:120deg; 
            --sinus:0.866; /* = sinus(angle) */
            width: 330px;
            height: 4px;
            // background-color: #00000036;
            background: linear-gradient(124deg, rgba(255,0,0,1) 0%, rgba(243,255,0,1) 25%, rgba(0,247,255,1) 50%, rgba(37,0,255,1) 76%, rgba(255,0,179,1) 100%);
            background-size: calc(var(--d)/var(--sinus)) 100%;
            animation: InfiniteMove 3s linear infinite reverse;
            /* border: 3px solid black; */
            /* padding: 1px; */
            border-radius: 8px;
            // margin-top: 40px;
            position: relative;
            //overflow: hidden;

            .slider-scrubber {
                position: absolute;
                width: 20px;
                height: 20px;
                border-radius: 20px;
                cursor: pointer;
                top: -8px;
            }
            
            .slider-inner {
                position: absolute;
                height: 100%;
                width: 100%;
                border-radius: 10px;
                // top: -6px;
                background-color: white;
            }
        }
    }

    @keyframes InfiniteMove {
        0% {
          background-position: calc(var(--d)/var(--sinus)) 0;
        }
      }

    .shuffle-buttons.lit {
        .shuffle-btn {
            animation-name: push-rainbow;
            animation-duration: 1s;
            box-shadow: rgb(240 46 170 / 0%) -5px 5px, rgb(240 46 170 / 0%) -10px 10px, rgb(240 46 170 / 0%) -15px 15px, rgb(240 46 170 / 1%) -20px 20px, rgb(240 46 170 / 0%) -25px 25px;
        }
    }

    .button-wrapper {
        z-index: 1000;
        margin-right: 10px;
        width: 42px;
        .button-text {
            text-align: center;
            width: 100%;
            font-size: 10pt;
            margin-bottom: 5px;
        }
    }
    .shuffle-buttons {
        position: absolute;
        left: 0px;
        right: 0px;
        margin: auto;
        bottom: 0;
        top: 460px;
        height: 92px;
        width: 380px;
        display: flex;
   }

    .shuffle-btn:active {
        animation-name: push-rainbow;
        animation-duration: 4s;

        box-shadow: rgba(240, 46, 170, 0.4) -1px 1px, rgba(240, 46, 170, 0.3) -2px 2px, rgba(240, 46, 170, 0.2) -3px 3px, rgba(240, 46, 170, 0.1) -4px 4px, rgba(240, 46, 170, 0.05) -5px 5px;
    }

    .circle-icon {
        background-color: white;
        width: 20px;
        height: 20px;
        border-radius: 20px;
    }

    .right-triangle-icon {
         width: 0; 
         height: 0; 
         border-right: 10px solid transparent;
         border-left: 10px solid transparent;
         
         border-bottom: 20px solid white;
    }

    .shuffle-btn {
        margin-right: 10px;
        display: flex;
        border-radius: 10px;
        height: 42px;
        text-align: center;
        background-color: black;
        color: white;
        cursor: pointer;
        z-index: 1000;
        padding: 10px;
        width: 42px;


        img {
            width: 23px;
        }
    }
}

.nft-jacker {
    .shuffle-buttons {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    .nft-jacker.jacked {
        .buy-unsold-btn {
            display: none;
        }

        .shuffle-buttons {
            margin: unset;
            top: unset;
            bottom: 60px;
            margin-left: auto;
            margin-right: auto;

            .play-btn-wrapper {
                // margin-top: 23px;
                width: 40px;
                height: 42px;
                padding: 9px;
                padding-top: 8px;
            }
        }

    }
}

@keyframes push-rainbow {
    from {
        background-color: #747474;
        box-shadow: rgba(240, 46, 170, 0.4) -5px 5px, rgba(240, 46, 170, 0.3) -10px 10px, rgba(240, 46, 170, 0.2) -15px 15px, rgba(240, 46, 170, 0.1) -20px 20px, rgba(240, 46, 170, 0.05) -25px 25px;
    }
    to {
        background-color: black;
        box-shadow: rgb(240 46 170 / 0%) -5px 5px, rgb(240 46 170 / 0%) -10px 10px, rgb(240 46 170 / 0%) -15px 15px, rgb(240 46 170 / 1%) -20px 20px, rgb(240 46 170 / 0%) -25px 25px;
    }
}
