.glossary-items {
    padding-top: 60px;
    display: grid;
    grid-gap: 25px;
    gap: 25px;
    max-width: 1100px;
    grid-template-columns: repeat(auto-fill,minmax(350px,1fr));
    justify-content: center;
    width: 90%;
    justify-items: center;
    align-items: center;
    margin: auto;

    .helper-item {
        p {
            max-width: unset !important;
            margin-left: unset;
            margin-right: unset;
        }
    }

    .helper-item.item-1 {
        background: #2e1bea;
        background: linear-gradient(146deg,#2e1bea,#17dbf0);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#2e1bea",endColorstr="#17dbf0",GradientType=1);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .helper-item.item-2 {
        background: #ea251b;
        background: linear-gradient(146deg,#ea251b,#f0a817);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ea251b",endColorstr="#f0a817",GradientType=1);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .helper-item.item-3 {
        background: #17f0cf;
        background: linear-gradient(146deg,#17f0cf,#e4ea1b);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#17f0cf",endColorstr="#e4ea1b",GradientType=1);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .helper-item.item-4 {
        background: #bc8eff;
        background: linear-gradient(146deg,#bc8eff,#501bea);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#bc8eff",endColorstr="#501bea",GradientType=1);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .helper-item.item-5 {
        background: #dce220;
        background: linear-gradient(145deg,#dce220,#ff89ef);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#dce220",endColorstr="#ff89ef",GradientType=1);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .helper-item.item-6 {
        background: #d617f0;
        background: linear-gradient(146deg,#d617f0,#ea1b1b);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#d617f0",endColorstr="#ea1b1b",GradientType=1);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    
    .helper-item {
        margin-right: 15px;
        font-size: 35px;
        font-weight: 700;
        text-align: left;
        border-radius: 25px;
        background-color: #1b26ea;
        height: 200px;
        width: 300px;
        padding: 0 20px;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#17f0cf",endColorstr="#e4ea1b",GradientType=1);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

        .glossary-number {
            font-size: 12px;
            margin-bottom: 0;
            margin-top: 15px;
        }

        .glossary-term {
            font-size: 20px;
            margin-top: 0;
        }

        .glossary-definition {
            font-size: 15px;
            margin: 0;
        }
    }
}

