.slice-recorder {
    position: absolute;
    left: 120px;
    bottom: 65px;
    border-radius: 20px;
    width: 40px;
    height: 40px;
    background-color: red;
    border: 10px solid black;
    z-index: 100000;
}

@media screen and (max-width: 600px) {
   .slice-recorder {
       left: unset;
       right: 70px;
       bottom: 25px;
       width: 30px;
       height: 30px;
   }
}
