
.nft-jacker.jacked {
    .nft-link {
        .jack-history {
            width: 100%;
            max-height: 80vh;
        }
    }
}
.nft-jacker {
    
    .nft-link {
    //    position: relative;

        .jack-history {

            img.globe {
                position: absolute;
                width: 20px;
                height: 20px;
                bottom: 10px;
                right: 10px;
            }
            position: absolute;
            top: 85px;
            left: 0px;
            width: 100%;
            background-color: #272727db;
            backdrop-filter: blur(4px);
            z-index: 1000000000;
            max-height: 35vh;
            display: flex;
            flex-direction: column;
            border: 1px solid #ffffff4a;
            border-radius: 3px;

            .items {
               height: 100%;
                overflow-y: scroll;
                overflow-x: hidden;
                .history-item:hover {
                    background-color: white;
                    color: black;
                }

                .history-item.current {
                    background-color: white;
                    color: black;
                }

                .history-item.spore-track {
                    .source {
                        margin-left: auto;
                        font-size: 9pt;
                        line-height: 22px;
                        color: gray;
                    }

                    img {
                        margin-left: 10px;

                    }
                }
                .history-item {
                    font-size: 11pt;
                    cursor: pointer;
                    padding: 6px;
                    padding-left: 10px;
                    display: flex;
                    white-space: nowrap;

                    .history-item-source {
                        font-size: 9pt;
                        line-height: 22px;
                        color: gray;
                        margin-left: 10px;
                        display: flex;
                        flex: 1;

                        .source {
                            margin-left: auto;
                        }
                    }
                    
                    img {
                        animation: explainer-blink .72s ease-in-out infinite;
                        animation-direction: alternate;
                        margin-left: auto;
                        width: 20px;
                        height: 20px;
                    }
                }
            }
        }

    }
}

@media screen and (max-width: 600px) {

    .nft-jacker.jacked {
        .nft-link {
            // left: 12px !important;
            .jack-history {
                width: 90% !important;
            }
        }
 
    }
    .nft-jacker {
        .nft-link {
            .jack-history {
                width: 100% !important;
                font-size: 11pt;
                max-height: 50vh;
            }
        }
    }
}
