.spore-modal-dialog {
    .spore-recorded-dialog {
        display: flex;
        flex-direction: column;
        flex: 1;

        .connect-button-wrapper {
            button {
                div {
                    background-color: white;
                    color: black;
                }
            }
        }
        .spores-buy-helper {
            margin-top: 15px;

            .buy-unsold {
                margin-top: 15px;
                .buy-unsold-btn {
                    margin-left: auto;
                    margin-right: auto;
                    background-color: white;
                    color: black;
                    padding-left: 10px;
                    padding-right: 10px;
                    border-radius: 10px;
                }

                .unsold-remaining {
                    margin-left: auto;
                    margin-right: auto;
                }
            }
        }
        .inner-spore-recorded, .sign-recording-container {
            width: 100%;
            display: flex;
            flex-direction: column;
            margin-top: 30px;

            a {
                color: white;
                text-decoration: underline;
            }

            a:hover {
                color: black;
                background-color: white;
            }

            .sign-btn, .continue-remixing-btn {
                padding: 8px;
                padding-left: 12px;
                padding-right: 12px;
                background-color: white;
                border-radius: 10px;
                color: black;
                margin-left: auto;
                margin-right: auto;
            }

            .sign-helper {
                font-size: 9pt;
                width: 280px;
                margin-left: auto;
                border: 1px solid white;
                position: relative;
                margin-top: 20px;
                padding: 10px;
                background-image: linear-gradient(124deg, rgba(255,0,0,1) 0%, rgba(243,255,0,1) 25%, rgba(0,247,255,1) 50%, rgba(37,0,255,1) 76%, rgba(255,0,179,1) 100%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: #ffffff4a;
                font-weight: bold;
            }

            .sign-helper:after {
                width: 0;
                height: 0;
                background-color: unset;
                border-left: 6px solid transparent;
                border-right: 6px solid transparent;
                border-bottom: 6px solid white;
                border-bottom: 6px solid white;
                content: "";
                position: absolute;
                left: 50px;
                top: -6px;
            }
        }



        .spore-header {
            font-size: 16pt;
            font-weight: 600;
        }

        .uploading-text {
            margin-top: 15px;
        }

        .progress-container {
            display: flex;
            width: 100%;
            margin-top: 30px;
            .progress-bar {
                background-color: #aebccff0 !important;
                border: 1px solid black;
                border-radius: 8px;
                margin-left: auto;
                margin-right: auto;

                .inner-progress-bar {
                    .progress {
                        background: linear-gradient(to right, #ffb3b3, #53ff00);
                    }
                }
            }

        }

        .spore-modal-footer {
            margin-top: auto;
            width: 100%;
            display: flex;

            .save-btn {
                margin-left: auto;
                padding: 8px;
                padding-left: 14px;
                padding-right: 14px;
                background-color: #4cff4c;
                border-radius: 10px;
                color: black;
                cursor: pointer;
            }
        }
    }
}
