.link-and-buy {
    display: flex;
    margin-top: 28px;

    a.align-right  {
        margin-left: auto;
        margin-top: -15px;
    }
    a  {
        line-height: 32px;
        margin-left: 15px;
    }
}

.buy-unsold.has-text {
    .buy-unsold-btn {
        margin-top: 0px;
        margin-left: auto;
        border-radius: 0px;
        background-color: transparent;
        border-bottom: 1px solid black;
        padding-bottom: 4px;
        span {
            color: white;
            -webkit-mask:unset;
        }
    }
}

.buy-unsold {
    font-size: 12pt;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: auto;

    .finalize-modal-drop  {
        position: fixed;
    }


    .buy-unsold-btn {
        margin-right: 10px;
        align-items: flex-start;
        background-color: black;
        color: white;
        padding: 8px;
        padding-left: 1px;
        padding-right: 1px;
        border-radius: 8px;

        span {
            background: -webkit-linear-gradient(60deg, red 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
            background: -moz-linear-gradient(60deg, red 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
            background: linear-gradient(60deg, red 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            display: inline-block;
            -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
            background-repeat: no-repeat;
            padding-left: 8px;
            padding-right: 8px;
            animation: shimmer 2.7s infinite;
        }
    }

    a.unsold-remaining {
        margin-left: unset;
        color: white;
        align-items: flex-start;
        line-height: 38px;
        font-size: 10pt;

    }
}


@media screen and (max-width: 600px) {

    .buy-unsold.has-text {
        position: fixed;
        bottom: 15px;
        left: 15px;
        .buy-unsold-btn {
        }
    }

    .link-and-buy.keyon {
        position: fixed;
        margin-top: 70px;
        width: 100%;

        a {
            margin-top: -15px;
        }
    }
    .link-and-buy {
        display: flex;
        top: 80px;
        left: 10px;
        width: 100px;
        margin-top: 20px;

        a {
            line-height: 55px;
            // margin-right: 80px;
            // margin-left: auto;
            margin-top: 0px;

        }

        a.align-right  {
            margin-left: 15px;
            margin-top: -35px;
            .source {
                font-size: 10pt !important;
                line-height: 65px !important;
            }
        }
 
        .buy-unsold {
            margin-left: 0px;
            .buy-unsold-btn {
                font-size: 8pt;

            }
        }
    }
}
