
@media screen and (max-width: 600px) {
    .time-remaining {
        font-size: 16pt !important;
        top: 80px;
        margin-top: unset;
    }
}

.time-remaining {
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    color: red;
    width: 75px;
    font-size: 28pt;
    opacity: 1;
}

.fade.time-remaining {
    animation: fade-time;
    animation-duration: 1.5s;
    opacity: 0.5;
}

@keyframes {
    from {
        opacity: 1;
    }
    to {
        opacity: .5;
    }
}
