

@media screen and (max-width: 600px) {
    .next-gen.zpore {
        .zs-container {
            width: 81% !important;
            .zorb-selector {
                width: 100% !important;
                font-size: 10pt;
                
                .connect-button-wrapper {
                    margin-top: 15px !important;
                }
            }
        }
    }
}

.connected {
    .zorb-selector {
        top: 60px;
    }
}

.next-gen.zpore {
    .spore-embed-helper {
    //    display: none;
    }

    .zs-container {
        position: absolute;
        bottom: 10px;
        left: 0px;
        right: 0px;
        width: 50%;
        margin-left: auto;
        margin-right: auto;
        z-index: 10000;
    }
    .zorb-selector {
        max-width: unset;
        top: 10px;
        margin-left: auto;
        margin-right: auto;
        width: 80%;
        padding: 10px;

        .no-zorbs {
            color: #ff0038;
            margin-top: 10px;
            text-align: center;
        }

        .connect-button-wrapper {
            margin-left: auto;
            margin-right: auto;
            margin-left: auto;
            margin-right: auto;
            width: 130px;
            margin-top: 20px;
            font-size: 10pt;

            button {
                div {
                    background-color: white !important;
                    color: black !important;
                }
            }
        }
    }
}

.zorb-selector {
    top: 10px;
    z-index: 100000;
    max-width: 60vw;

    .zorbs-list {
        margin-top: 10px;
        display: flex;

        img.unselected:hover {
            animation-name: hover-logo-future;
            animation-duration: 2s;
            filter: brightness(1.5);
        }
        
        .zorb-option {
            border: 1px solid transparent;
        }

        img.selected {
            animation-duration: 1s;
            animation-name: fade-zorb-out;
        }

        .zorb-option.selected {
            border-bottom: 1px solid #ffffff;
        }
        img {
            filter: brightness(1);
            cursor: pointer;
            width: 30px;
        }
    }
}

@keyframes fade-zorb-out {
    0% {
        opacity: 0;
    }

    5% {
        opacity: 0;
    }

    100% {
        opacity: 0;
    }
}
