
.spore-modal-backdrop {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: 4;
    background-color: #676767af;
    //backdrop-filter: blur(8px);
    display: flex;

    iframe.modal-iframe {
        animation-name: fade-in-shader;
        animation-duration: 8s;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 5;
        top: 0px;
        border-radius: 0px;
        opacity: 0.3;
        border: transparent;
        left: 0px;
    }

    .spore-modal-dialog {
        z-index: 20;
        width: 500px;
        height: 400px;
        margin: auto;
        background-color: black;
        border: 3px solid black;
        padding: 30px;
        color: white;
        padding: 30px;
        display: flex;
        flex-direction: column;
        border-radius: 15px;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

        .logo-container {
            display: flex;
            width: 100%;
            margin-bottom: 15px;
            img {
                width: 30px;
                height: 30px;
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .spore-modal-backdrop {
        .spore-modal-dialog {
            width: 100%;
            height: 450px;
        }
    }
}
