.gen3-info {
    background-color: #000000db;
    display: flex;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 100000000;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(8px);

    .page-navigator {
        display: flex;
        position: absolute;
        left: 0px;
        right: 0px;
        bottom: 25px;
        margin-left: auto;
        margin-right: auto;
        width: 100px;

        .page-info {
            margin-left: 15px;
            font-size: 13px;
            margin-right: 15px;
            line-height: 10px;
        }
    }

    .prev-btn.hide, .next-btn.hide {
        pointer-events: none;
        opacity: 0;
    }
    
    .prev-btn, .next-btn {
        cursor: pointer;
        width: 0px;
        height: 0px;
        border: solid white;
        border-width: 0 1px 1px 0;
        display: inline-block;
        padding: 4px;
    }

    .prev-btn {
        transform: rotate(135deg);
        -webkit-transform: rotate(135deg);
    }

    .next-btn {
        transform: rotate(-45deg);
        -webkit-transform: rotate(-45deg);
    }

    .exit-btn {
        position: absolute;
        top: 15px;
        right: 35px;
        cursor: pointer;
        font-size: 48px;
    }
    
    img {
        height: 80%;
        margin: auto;
        object-fit: contain;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    }
}

.gen3-btn {
    font-size: 16pt;
    z-index: 1000000;
    position: absolute;
    bottom: 30px;
    left: 30px;
    background-color: black;
    color: white;
    padding: 15px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

    .gen3-text {
        background: linear-gradient(60deg, rgb(3 238 254) 0%, rgb(133 117 255 / 88%) 25%, rgb(181 60 238) 50%, rgb(255 51 198) 75%, rgb(255 43 177) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        display: inline-block;
        -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
        background-repeat: no-repeat;
        animation: shimmer 2.5s infinite;
    }
}

@media screen and (max-width: 600px) {
    .gen3-btn {
        bottom: 5px;
        font-size: 12pt;
        padding: 10px;
        left: 0px;
        right: 0px;
        width: 125px;
        margin-left: auto;
        margin-right: auto;
    }
}
