
@media screen and (max-width: 600px) {
    .video-jacker.nft-jacker.drop.dark-setting {
        .spore-embed-container {
            background: linear-gradient(45deg, rgb(9 8 8) 0%, rgb(0, 0, 0) 50%, rgb(0 50 50 / 36%) 100%) !important;
        }
    }
    .video-jacker.nft-jacker.drop {

        .video.hide {
            display: none;
        }
        .play-controls {
            bottom: unset;
            top: 8px;
            //bottom: 14px;
            img {
                width: 35px;
            }
            img.rew {
                margin-left: 90px;
            }
            img.fwd {
                margin-right: 90px;
            }
            transform: scale(1) translate(0, 90px);
            width: 100%;
            transform-origin: center;
            opacity: 0.9;
            .play-btn-wrapper {
                padding: 0px;
                width: 20px;
                height: 30px;
                margin-right: auto;
                margin-left: auto;
            }
        }
        
        .video-backdrop-shader {
            canvas {
                top: unset;
                bottom: 0px;
                height: 75% !important;
                opacity: 0.6;
            }
        }

        iframe {
            background-color: transparent;
            transform: translate(0px, 35px) !important;

        }
        .spore-embed-container {
//            z-index: 100;
            //background: linear-gradient(124deg, rgba(0, 0, 0, 0.8), rgba(69, 65, 65, 0.8) 50%, rgba(17, 10, 15, 0.8)), url(https://grainy-gradients.vercel.app/noise.svg) !important;
           // background: linear-gradient(124deg, rgba(0, 0, 0, 0.8), rgb(141 132 132 / 22%) 50%, rgb(17 10 15 / 54%)), url(https://grainy-gradients.vercel.app/noise.svg) !important;
            //background: linear-gradient(124deg, rgb(0 0 0 / 89%), rgb(63 61 61 / 85%) 50%, rgb(17 10 15 / 82%)), url(https://grainy-gradients.vercel.app/noise.svg) !important;
            background: linear-gradient(124deg, rgba(0, 0, 0, 0.89), rgb(12 2 2 / 85%) 50%, rgb(1 1 1 / 82%)), url(https://grainy-gradients.vercel.app/noise.svg) !important;
            
            .shuffle-buttons {
                top: 0px !important;
                left: 0px !important;

                .play-btn-wrapper {
                    margin-top: 12px;
                    padding-top: 10px;
                    padding-left: 12px;
                }
                
                .play-and-slider {
                    position: absolute;

                    .slider-scrubber {
                        width: 8px;
                        height: 8px;
                        top: -2px;
                    }

                    .slider-container {
                        width: 100vw;
                        height: 3px;
                        position: absolute;
                        top: 0px;
                    }
                }
            }

            svg {
                display: none;
            }
            .spore-embed-helper {
                position: fixed;
                background-color: #00000096;
                border: 1px solid #ffffff33;
                backdrop-filter: blur(8px);
                padding: 8px;
                width: 100%;
                font-size: 14pt;
                bottom: 0px;
                top: unset;
                left: 0px;
                font-size: 10pt !important;
                //z-index: 10000;

                .song-title, .song-artist {
                    padding-left: 45px;
                }
                .song-artist {
                    color: #897e7e;
                }
            }
        }
        
        video, .video {
            top: 0px;
            margin: unset;
            width: 100%;
            height: unset;
        }
    }
}
