.nft-jacker {
    .player-container.show {
        display: flex;
    }
}

        .remix-player {
            .ens-name {
                font-size: 11pt;
                color: white;
            }
            margin: auto;
            position: relative;
            z-index: 100000;

            .share-button {
                top: 155px;
                left: 5px;

                svg {
                    fill: white;    
                }
            }

            .close-button {
                position: absolute;
                font-size: 16pt;
                right: 25px;
                top: 0px;
                cursor: pointer;
            }

            .audio-player.disabled {
                pointer-events: none;
            }
            
            .audio-player {
                .progress-container {
 
                   display: flex;
                    width: 100%;
                    height: 100%;
                    border-radius: 25px;
                    margin: auto;
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    right: 0;
                    left: 0;
                    background-color: black;
                    justify-content: center;
                    align-items: center;
                    z-index: 2;
                    .progress-bar {
                        margin-left: auto;
                        margin-right: auto;
                    }
                }
                display: flex;
                margin-left: -30px;
                background-color: black;
                padding: 30px;
                border-radius: 30px;
                box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
                iframe.spore-heatmap {
                    margin: unset;
                    position: unset;
                    border: 4px solid white !important;
                }
            }
        }

@media screen and (max-width: 600px) {
    .nft-jacker {
        .player-container.show {
            .remix-player {
                .audio-player {
                    margin-left: 0px;
                    padding: 20px;
                }
                .share-button {
                    left: 25px;
                }
            }
        }
    }
}
