.slicer-backdrop {
    position: absolute;
    z-index: 100000;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #f8f8f86e;
    backdrop-filter: blur(8px);
    display: flex;

    .download-test {
        position: absolute;
        background-color: #1d1d1d;
        padding: 15px;
        width: 300px;
        height: 80px;
        border-radius: 15px;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;

        .progress-bar {
            margin-top: -10px;
        }
        a {
            color: #7eff17;
            text-decoration: underline;
        }
    }
}

.slice-waveform {
    position: absolute;
    bottom: 90px;
    margin-left: auto;
    margin-right: auto;
    left: 0px;
    right: 0px;
    width: 50%;
    height: 100px;
    background-color: #1d1d1d;
    border-radius: 10px;
    z-index: 100000;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

    display: flex;

    .control-btn-container {
        border-right: 2px solid black;
        width: 80px;
        height: 100%;
        margin-right: 10px;

        .play-btn-wrapper {
            padding: 0px !important;
            margin-top: unset;
            width: 80px;
            height: 100%;
            border: none;
            display: flex;

            .play-btn {
                margin: auto;
            }
        }
        
    }

    .waveform-container {
        position: relative;
        flex: 1;
    }

    svg {
        margin-left: 30px;

        rect.sliced-region {
            opacity: 1;
        }
        rect {
            opacity: 0.5;
        }
    }

    .playhead {
        position: absolute;
        top: 0px;
        width: 10px;
        transform: translate(-7px, 0);
        height: 100%;
        background-color: #ff000000;
        background-image: linear-gradient(89deg, transparent, #ff4300);
    }

    .slicer {
        position: absolute;
        top: 0px;
        height: 100px;
        border: solid 15px #77dd1b;
        background-color: transparent;
        border-radius: 15px;
        display: flex;

        .arrow-right, .arrow-left {
            border: solid black;
            border-width: 0 8px 8px 0;
            border-radius: 3px;
            margin-top: auto;
            margin-bottom: auto;
            width: 0px;
            display: inline-block;
            padding: 8px;
        }

        .arrow-right {
            margin-right: 6px;
            transform: rotate(-45deg);
        }
        .arrow-left{
            margin-left: 6px;
            transform: rotate(135deg);
        }
        
        .start-control:active, .end-control:active {
            .start-info {
                opacity: 1;
                animation-name: tooltip-fade;
                animation-duration: 0.5s;
            }
            .arrow-right, .arrow-left {
                border-color: white;
                animation-name: slicing;
                animation-duration: 0.5s;
            }
        }
        .start-control, .end-control {
            .start-info::before {
                content: "";
                width: 20px;
                position: absolute;
                height: 20px;
                bottom: -20px;
                left: 20px;
                border: 10px solid red;
                border-color: #353543 transparent transparent transparent;
            }
            .start-info {
                top: -50px;
                width: 120px;
                height: 40px;
                padding: 8px;
                position: absolute;
                background-color: #353543;
                color: white;
                border-radius: 8px;
                opacity: 0;
                display: flex;

                span {
                    margin-top: 5px;
                    margin-left: auto;
                    color: gray;
                }
            }
            display: flex;
            background-color: #77dd1b;
            height: 100px;
            cursor: pointer;
            width: 30px;
        }
        .start-control {
            position: absolute;
            left: -15px;
            top: -15px;
            height: 100PX;
            BORDER-top-left-radius: 15px;
            BORDER-bottom-left-radius: 15px;
        }

        .end-control {
            position: absolute;
            right: -15px;
            top: -15px;
            height: 100PX;
            BORDER-top-right-radius: 15px;
            BORDER-bottom-right-radius: 15px;
        }
    }

    .slice-guide-info {
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        background-color: #1d1d1d;
        padding: 15px;
        border-radius: 15px;
        position: fixed;
        bottom: 250px;
        width: 260px;
    }

    .captioner {
        display: flex;
        height: 100%;
        
        input {
            height: 30px;
            margin-top: auto;
            margin-bottom: auto;
            width: 90%;
            background-color: transparent;
            outline: none;
            border-bottom: 1px solid white;
        }
    }


}

.slicer-backdrop {
    .slice-toolbar.captioning {
        width: 20%;
        animation-name: shrink-toolbar;
        animation-duration: 1s;
        background-color: #e4e4e4;
        min-width: 300px;
    }

    .slice-toolbar {
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        position: fixed;
        display: flex;
        min-width: 300px;
        height: 50px;
        font-size: 14pt;
        width: 30%;
        left: 0px;
        right: 0px;
        bottom: 0px;
        margin-left: auto;
        margin-right: auto;
        color: black;
        background-color: #b7b7b7;
        padding: 10px;
        padding-left: 30px;
        padding-right: 30px;
        border-top-right-radius: 15px;
        border-top-left-radius: 15px;

        .done-btn.continue {
            margin-left: auto;
            margin-right: auto;
        }
        .done-btn {
            color: black;
            background-color: #5bff15;
            background-image: linear-gradient(45deg, #baff1d, transparent);
            padding-left: 15px;
            padding-right: 15px;
            border-radius: 12px;
        }
        .done-btn {
            margin-left: auto;
        }
    }
}


@media screen and (max-width: 600px) {
    .slice-waveform {
        width: 400px;
        height: 60px;

        .slicer {
            height: 60px;
            .start-control, .end-control {
                height: 60px;

                .arrow-right, .arrow-left {
                    border-width: 0 5px 5px 0;
                }
            }
        }

    }
}

@keyframes slicing {
    from {
        border-color : black;
    }

    to {
        border-color: white;
    }
}

@keyframes tooltip-fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}
@keyframes shrink-toolbar{
    from {
        width: 30%;
        background-color: #b7b7b7;
    }

    to {
        width: 20%;
        background-color: #e4e4e4;
    }
}


