.tab-container {
    display: flex;
    z-index: 1;
    position: absolute;
    left: 20px;
    top: 90px;
    border-radius: 15px;
    font-size: 11pt;
    border-color: white;
    border-style: solid;
    border-width: 2px;
    overflow: hidden;
    z-index: 100000;

    .tab {
        padding: 10px 15px;
        padding-top: 3px;
        padding-bottom: 3px;
        width: fit-content;
        height: fit-content;
        color: black;
        text-align: center;
        cursor: pointer;
    }

    .tab.not-selected {
        background-color: black;
        color: darkgray;
    }

    .tab.selected {
        background-color: white;
    }
}

