.playlist-container {
    overflow: scroll;
    height: 100%;
    overflow-x: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    -ms-overflow-style: none;
    scrollbar-width: none;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0 10px;
    padding-top: 140px;
    backdrop-filter: blur(3px);

    .playlist-item {
        width: 100%;

        .playlist-description {
            width: 100%;
            margin-bottom: 5px;
            text-align: center;
        }

        p {
            width: 38%;
        }
    }

    .playlist-trigger {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        padding: 20px 0px;

        p {
            font-size: 25px;
        }

        .playlist-trigger-img {
            width: 98%;
            height: 150px;
            border-radius: 25px;
            object-fit: cover;
        }
    }

    .playlist-list-container {
        border-radius: 20px;
        height: fit-content;
        background-color: #20202061;
        backdrop-filter: blur(3px);
        width: 100%;
        padding-bottom: 25px;
        display: flex;
        flex-direction: column;
        align-items: center;

        .playlist-item {
            display: flex;
            flex-direction: row;
            margin: 5px 0px;
            align-items: center;
            justify-content: space-between;
            padding: 10px;
            cursor: pointer;
            width: 95%;

            .playlist-item-img {
                width: 25px;
                border-radius: 5px;
            }
        }

        .playlist-item:hover {
            background-color: rgba(62, 62, 62, 0.499);
            border-radius: 15px;
        }
    }
}

.music-chart {
    overflow: scroll;
    height: 100%;
    overflow-x: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    -ms-overflow-style: none;
    scrollbar-width: none;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 0 10px;
    padding-top: 140px;

    .chart-section-header {
        width: 100%;
        margin-left: 10px;

        .chart-header-name {
            font-weight: 700;
            font-size: 25px;
        }

        .chart-header-by {
            font-weight: 500;
        }
        p {
            color: white;
        }
    }

    .chart-section-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .progress-bar {
        margin: auto;
    }

    .song-info-container {
        float: left;
        display: flex;
        flex-direction: column;
    }

    .song-info {
        display: flex;
        margin-top: 5px;

        .sold {
            color: red;
            font-size: 8pt;
        }
        a {
            color: darkgray;
        }

        a:hover {
            text-decoration: underline;
        }
        
        .artist {
            font-size: 15px;
        }

        .song {
            font-weight: 700;
            display: flex;
            justify-content: space-between;
        }
    }

    .chart-item {
        margin: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        align-content: center;
        height: 290px;
        width: 200px;
    }

    .from-text {
        float: left;
        font-size: 12px;
        color: darkgray;
        display: flex;
        width: 100%;
   }
    .num-remixes {
        display: flex;
        margin-top: 5px;
        padding-left: 3px;
        padding-right: 5px;
        height: 15px;
        background-color: white;
        color: #3c3c3c;
        text-align: center;
        font-size: 12pt;
        border-radius: 15px;
        line-height: 15px;
        margin-left: auto;

        .remixes-orb {
            margin-top: 3px;
            width: 10px;
            height: 10px;
            border-radius: 10px;
            background-color: red;
            box-shadow: rgb(240 46 222 / 12%) 5px 15px, rgb(240 46 170 / 18%) -10px 10px, rgb(240 46 239 / 9%) -5px 15px, rgb(240 46 170 / 6%) -20px 20px, rgb(240 46 170 / 7%) -25px 25px;
            background-image: radial-gradient( 85.29% 65.29% at 30.96% 24.36%, #ff0066 .62%, #ff004c 30.21%, #ff6700 80.71%, #fe8a9c 55.73%, #311f122b 9.69%, #b3ffe9 0.62%, #2d1c13f0 30% );
            margin-left: 5px;
            margin-right: 5px;
        }
    }
    
    .cover-image{
        width: 200px;
        height: 200px;
        border-radius: 10px;
        // border-style: solid;
        // border-width: 1px;
        // border-color: white;
    }
}

@media screen and (max-width: 600px) {

    .nft-jacker.jacked {

        .playlist-container {
            width: 100%;
            z-index: 100000;
        }
        .tab-container {
            z-index: 1000000;
            top: 80px;
            font-size: 8pt;
            right: 15px;
            width: 300px;
    
            .tab {
                width: 25vw;
                padding: 10px;
                padding-top: 7px;
                padding-bottom: 7px;
            }
        }
    
        .music-chart {
            top: 130px;
            justify-content: center;
            width: 100%;
            background-color: #000000e8;
            backdrop-filter: blur(4px);
            border-radius: 0px;
        }
    }


    .tab-container {
        z-index: 10000000;
        top: 65px;
        left: 8vw;
        font-size: 8pt;
        margin-left: 0px;
        height: 50px;
        .tab {
            width: 28vw;
            padding: 10px;
            padding-top: 7px;
            padding-bottom: 7px;
            display: flex;
            justify-content: center;
            align-content: stretch;
            height: 100%;
            align-items: center;
        }
    }

    .music-chart {
        height: 100%;
        top: 0px;
        justify-content: center;
        background-color: black;
        width: 100%;
        padding-top: 130px;
        position: fixed;
        padding-bottom: 80px;

        .chart-item {
