                    height: 34vw;
                    // border-radius: 0px;
                }
                .song-info {
                    .song {
                        font-size: 12px;
                        overflow-wrap: anywhere;
                    }
                    .artist {
                        font-size: 12px;
                    }
                }
            }
        }
    }
}
