
.next-gen.zpore.is-slicing {
    .slice-recorder, .simple-controls {
        display: none;
    }
}

.next-gen.zpore.hide-ui {

    .spore-embed-helper {
        display: none;
    }

    .zs-container {
        display: none;
    }

    .nft-jacker {
    }
}

.zpore.connected {
    .nft-jacker {
        z-index: unset !important;
    }
    .drop-top {
        display: block;
        z-index: 1000000000 !important;
    }
}

.zpore.next-gen {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    .nft-jacker.drop.thugger {
        .spore-embed-container {
            .spore-embed-helper {
                margin-top: -20px;
                font-size: 10pt;
                .song-title {
                    color: white;
                }

                .song-artist {
                    color: #d5d1d1;
                }
            }
        }
    }

    .simple-controls {
        position: absolute;
        bottom: 45px;
        left: 45px;
        z-index: 10000000000;

        .play-btn-wrapper {
            border: none;
            background: none;
            cursor: pointer;
        }
    }
    .drop.nft-jacker.jacked.thugger  {
        padding-bottom: unset;
        overflow-y: unset !important;
        .drop-description {
            //display: block !important;
        }
    }
    .drop-top {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    .next-gen.zpore {
        .simple-controls {
            position: absolute;
            bottom: 0px;
            left: unset;
            right: -15px;
        }
    }
}

