.nft-jacker {
    .nft-link.activating {
        input {
            animation-duration: 1s;
            animation-name: code-activation;
            color: #61fc9f;
            background-color: #131415;
        }
    }

    .nft-link {
        .activation-text {
            animation-duration: 10s;
            animation-name: fade-slow-out;
            opacity: 0;
            position: fixed;
            top: 140px;
            left: 20px;
            background-color: black;
            padding: 15px;
            width: 300px;
            border: 1px solid black;
            border-radius: 8px;
            border: 1px solid white;
            z-index: 1000000000;
        }

        .activation-text:after {
                width: 0;
                height: 0;
                background-color: unset;
                border-left: 6px solid transparent;
                border-right: 6px solid transparent;
                border-bottom: 6px solid white;
                border-bottom: 6px solid white;
                content: "";
                position: absolute;
                left: 50px;
                top: -6px;
            }
    }
}

@keyframes code-activation {
    from {
        background-color: white;
        color: red;
    }
    to {
        background-color: #131415;
    }
}

@keyframes fade-slow-out {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

