.video-jacker.nft-jacker.drop {
    overflow: hidden;
    background-color: black;
    
    .play-controls {
        display: flex;
        width: 300px;
        bottom: 60px;

        .play-btn-wrapper {
            margin-left: auto;
            margin-right: auto;
            width: 25px;
            padding: 0px;
            cursor: pointer;

            .play-btn {
            }
        }

        img {
            margin-top: 10px;
            width: 30px;
        }
    }
    .cover-image {
        display:  none;
    }

    .play-btn-wrapper {
        border: unset;
        background-color: unset;
        transform: scale(1.8);
        transform-origin: top left;
        width: unset;
        height: unset;
        padding-top: unset;
        margin-top: unset;
    }


    .video-backdrop-shader {
        canvas {
            width: 100% !important;
            height: 100% !important;
            top: 0px;
            left: 0px;
        }
    }

    .song-waveform {
        z-index: 7;
    }

    .drop-description {
        z-index: 3;

        p {
            margin-bottom: 20px;
        }

        padding-top: 40px;
        text-align: center;
        background: linear-gradient(90deg, #0d0d0dd6, #000000bf), url(https://grainy-gradients.vercel.app/noise.svg) !important;
        height: -moz-fit-content;
        height: fit-content;
        font-weight: bolder;
        font-size: 25px;
        color: white;
    }

    .stems-unlocked {
        display: none;
    }

    video, .video {
        /*
        position: absolute;
        width: 80%;
        //height: 80%;
        z-index: 6;
        margin: auto;
        left: 0px;
        bottom: 0px;
        top: 0px;
        right: 0px;
        */
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    }

    .spore-embed-helper {
//        z-index: 7;
        top: unset;
        bottom: 20px;
        font-size: 16pt !important;
    }
    
    .spore-embed-container {
        min-height: 98vh;
        padding: 0px !important;
        padding-top: 30vh !important;
        /*
        background: linear-gradient(124deg, rgba(0, 0, 0, 0.8), rgb(141 132 132 / 22%) 50%, rgb(17 10 15 / 54%)), url(https://grainy-gradients.vercel.app/noise.svg) !important;
        background: linear-gradient(124deg, rgba(0, 0, 0, 0.8), rgb(141 132 132 / 78%) 50%, rgba(17, 10, 15, 0.54)), url(https://grainy-gradients.vercel.app/noise.svg) !important;
        */
        
        background: linear-gradient(45deg, rgb(0, 0, 0) 0%, rgb(18 17 17 / 86%) 50%, rgb(0 0 0 / 98%) 100%), url(https://grainy-gradients.vercel.app/noise.svg) !important;

        //position: absolute;

        .shuffle-buttons {
            top: 80vh;

            .play-and-slider {
                z-index: 8;
            }
        }

        .spore-embed-helper {
            .song-artist {
                color: #c7c7c7;
            }
        }
   }

    iframe {
        border: none !important;
        box-shadow: none !important;
        background-color: unset;
        transform: translate(0, -10%);
    }
}
