.progress-bar {
    z-index: 5;
    position: relative;
    // width: 200px;
    // height : 20px;
    width: 70px;
    margin: auto;
    height : 70px;
    border-radius: 35px;
    // background-color: #202020f7 !important;
    // background-color: #aebccff0 !important;
    // border: 1px solid black;
    // border-radius: 8px;
    //background-color: #0c71d6 !important;
    padding: 5px;
    animation: gradient 8s ease-in-out;
    animation-iteration-count: infinite;
    background: linear-gradient(124deg, rgba(255,0,0,1) 0%, rgba(243,255,0,1) 25%, rgba(0,247,255,1) 50%, rgba(37,0,255,1) 76%, rgba(255,0,179,1) 100%);
    background-size: 500% 500%;
    
    @keyframes gradient {
        0% {
            transform: rotate(0deg);
            background-position: 0% 50%;
        }
        50% {
            background-position: 100% 50%;
            transform: rotate(180deg);
        }
        100% {
            background-position: 0% 50%;
            transform: rotate(360deg);
        }
    }

    .inner-progress-bar {
        position: relative;
        .progress {
            border-radius: 0px;
            height: 3px;
            background-color: red;
            //background-image: linear-gradient(to right, #5e5ec5, #21e3f1);
            //background: linear-gradient(to right, #d1d1f3, #18adff78);
            background: linear-gradient(to right, #ffb3b3, #53ff00);
        }
        
        .loading-text {
            z-index: 6000;
            position: absolute;
            left: 20px;
            color: #d7d1d1;
            font-size: 14pt;
            top: 6px;
        }
    }
}

