.drop.thugger.nft-jacker {
    .spore-modal-backdrop {
        z-index: 1000;
    }
    .spore-embed-container {
        //background: linear-gradient(45deg, rgb(0, 0, 0) 0%, rgb(18 17 17 / 86%) 50%, rgb(0 0 0 / 98%) 100%), url(https://grainy-gradients.vercel.app/noise.svg) !important;
        background: linear-gradient(45deg, rgb(0, 0, 0) 0%, rgb(0 0 0 / 86%) 50%, rgba(0, 0, 0, 0.98) 100%), url(https://grainy-gradients.vercel.app/noise.svg) !important;
        .sample-info {
            top: 600px;
        }
    }

    .drop-description {
        padding-top: 100px;
        color: white;
        background: linear-gradient(90deg, rgb(0 0 0), rgb(0 0 0 / 89%)), url(https://grainy-gradients.vercel.app/noise.svg) !important;
    }

    padding-bottom: 200px;

    .remix-list-container {
        .no-remix-div {
            display: none;
        }
    }
}

@media screen and (max-width: 600px) {
    .drop.thugger.nft-jacker {
        .flex.w-full {
            margin-top: -50px;
        }

        .shuffle-buttons {
            top: 550px;
        }

        .remix-list-container {
            height: unset !important;
        }

        padding-bottom: unset;
    }
}
