.sample-info {
    position: absolute;
    border-radius: 20px;
    left: 0px;
    right: 0px;
    margin: auto;
    bottom: 0;
    top: 460px;
    height: 92px;
    width: 300px;
    display: flex;
    transform: translate(360px, 20px);
    background-color: black;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

    .sample-name {
        width: 100px;
        white-space: nowrap;
        overflow: hidden;
        font-size: 9pt;
        margin-bottom: 5px;
    }


    .name-and-arrows {
        margin-right: 10px;
    }
    .arrow-btn {
        width: fit-content;
        height: fit-content;
        cursor: pointer;
        position: relative;
        z-index: 1000000;
    }

    .arrow-btn.flip:before {
        transform: rotate(0deg) scale(.9);
    }

    .arrows {
        margin-left: -10px;
        display: flex;

        .arrow-btn.flip {
            margin-left: 30px;
        }
    }
    
    .arrow-btn:before {
        position: absolute;
        left: 5px;
        width: 24px;
        height: 25px;
        top: 0px;
        content: "";
        transform: rotate(180deg) scale(0.9);
        background-image: url("data:image/svg+xml,%3Csvg width='24' height='25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5 12.5h14M12 5.5l7 7-7 7' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
    }
}
