@font-face {
  font-family: 'DecimaMonoCyr';
  font-style: normal;
  font-weight: 300;
  src: url(/DecimaMonoCyr/font.woff) format('woff');
}

.nft-jacker.drop {
    overflow-y: scroll;
    font-family: DecimaMonoCyr !important;
    position: relative;

    .isRounded {
        border-radius: 25px;
    }

    .isUnderlined {
        text-decoration: underline !important;
    }

    .new {
        color: #f05117;
        font-size: 12pt;
        margin-bottom: 0px;
    }

    .blue-gradient {
        background: linear-gradient(124deg,#62d3d7,#7470e2 50%,#b7aeff);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }

    .purple-gradient {
        background: linear-gradient(124deg,#62d3d7,#7470e2 50%,#b7aeff);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }

    .orange-gradient {
        background: linear-gradient(145deg,#833ab4,#fd1d1d 50%,#fcb045);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }

    .yellow-gradient {
        background: linear-gradient(145deg,#dce220,#ffb52f);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }

    .green-gradient {
        background: linear-gradient(312deg, #4dfb2a, #70e2a5 50%, #aec6ff);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }
    
    .connect-button-wrapper.drop-top {
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 1000;
    }
    img.cover-image {
        border-radius: 15px;
        width: 200px;
        height: 200px;
        position: absolute; 
        right: 15px;
        top: 65px;
    }

    .buy-button {
        display: flex;

        .asking-price {
            margin-left: auto;
            .price-label {
                text-align: left;
                font-size: 10pt;
            }

            .price {
                font-size: 16pt;
            }
        }
        font-size: 16pt;
        font-family: Inter,sans-serif;
        margin-top: 0px;
        margin-bottom: 50px;
        background-color: white;
        color: black;
        padding: 15px;
        border-radius: 8px;
        width: 320px;
        text-align: center;
        margin-left: auto;
        margin-right: auto;

        .action {
            line-height: 50px;
            margin-left: 15px;
        }
    }

    .drop-description {
        color: black;
        padding-top: 40px;
        text-align: center;
        //background: linear-gradient(90deg,#333336d6,#494343bf),url(https://grainy-gradients.vercel.app/noise.svg)!important;
        background: linear-gradient(90deg, #939397d6, #9c9999bf), url(https://grainy-gradients.vercel.app/noise.svg) !important;
        height: fit-content;

        font-weight: bolder;
        font-size: 25px;
        color: black;

        .arrow-p {
            margin-top: 20px;
            color: white;
            font-size: 44pt;
        }

        p.bullet-p {
            text-align: left;
            font-size: 14pt;
        }

        .artist-p {
            margin-bottom: 20px;
        }

        .tagline-p {
            margin-bottom: 20px;
        }

        .action-p {
            margin-top: 20px;
        }

        p {
            max-width: 60%;
            margin-left: auto;
            margin-right: auto;
        }
    }

    .spore-overview {
        .overview-header {
            margin-top: 80px;
            font-size: 50px;
            font-weight: 700;
            color: white;
            margin-bottom: 60px;
        }

        .spore-overview-image {
            width: 340px;
            height: 400px;
            margin-left: auto;
            margin-right: auto;
        }
    }

    .remix-list-container {
        position: relative;
        top: unset;
        z-index: 2 !important;
    }

    .remix-backdrop {
        background: linear-gradient(90deg,#333336d6,#494343bf),url(https://grainy-gradients.vercel.app/noise.svg)!important;
        position: fixed;
        width: 100%;
        height: 100%;
        z-index: 10000;
        .remix-player {
            position: fixed;
            top: 0px;
            right: 0px;
            left: 0px;
            bottom: 0px;
            margin: auto;
            width: 600px;
            height: 200px;
        }
    }
    
    .drop-features {
        width: 800px;
        font-weight: bolder;
        margin: auto;
        font-size: 25px;
        color: var(--text-color);
        text-align: center;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        border-radius: 40px;
        padding: 25px 0;
        background: #1bea48;
        background: linear-gradient(146deg,#1bea48,#1781f0);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#1bea48",endColorstr="#1781f0",GradientType=1);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin-top: 50px;
        margin-bottom: 50px;
    }
    
    .spore-embed-container {
        position: relative;
        width: 100%;
        height: fit-content;
        //background: linear-gradient(124deg,rgba(49,108,110,.8),rgba(54,52,103,.8) 50%,rgba(74,70,105,.8)),url(https://grainy-gradients.vercel.app/noise.svg) !important;
        // background: linear-gradient(124deg,rgb(91 81 86 / 80%),rgb(57 116 150 / 80%) 50%,rgb(102 94 99 / 80%)),url(https://grainy-gradients.vercel.app/noise.svg)!important;
        background: linear-gradient(124deg, rgb(217 197 207 / 80%), rgb(111 190 238 / 80%) 50%, rgb(189 177 184 / 80%)), url(https://grainy-gradients.vercel.app/noise.svg) !important;
        padding-top: 220px;
        padding-bottom: 150px;

        .spore-embed-helper {
            position: absolute;
            font-weight: bolder;
            font-size: 25px;
            margin: auto;
            top: 75px;
            left: 50px;

            .song-artist {
                color: gray;
            }

            .stems-unlocked {
                margin-top: 15px;
                font-size: 16pt;
            }
        }

        .shuffle-buttons {
            top: 600px;

            svg {
                rect.elapsed {
                    fill: #ffffff9e;
                }
                rect {
                    //fill: #776d6d85;
                    fill: #a9989885;
                }
            }
        }

        .spore-flex-container {
            display: flex;
            width: 100%;
            
            iframe {
                box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
}


@media screen and (max-width: 600px) {
    .nft-jacker.drop::-webkit-scrollbar {
        display: none;
    }
    
    /* Hide scrollbar for IE, Edge and Firefox */
    .nft-jacker.drop {
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
    }
    .nft-jacker.drop {
        .spore-overview {
            img.spore-overview-image {
                margin-left: 20px;
            }
            .overview-header {
                text-align: center;
                font-size: 26pt;
            }
        }

        .glossary-items {
            .helper-item {
                height: unset;
                width: 320px;
            }
        }

        .drop-description {
            font-size: 14pt;
            //text-align: left;

            p {
                max-width: 90%;
            }
        }

        .spore-embed-container {
            .spore-embed-helper {
                font-size: 14pt;
                top: 70px;
                left: 30px;

                .stems-unlocked {
                    font-size: 10pt;
                }
            }
        }
        img.cover-image {
            width: 80px;
            height: 80px;
        }

        .drop-features {
            width: 90%;
        }
        .remix-list-container {
            height: 75vh !important;
        }
    }

    .remix-player {
        width: 99%;
    }

    .remix-list-container {
        position: relative;
    }
}

@media screen and (max-width: 800px) {
    .nft-jacker.drop {
        .drop-features {
            width: 100%;
        }

        .remix-backdrop {
            .remix-player {
                width: 93%;
                left: 30px;
                .audio-player .play-time-and-slider {
                    margin-left: unset;
                }
            }
        }
    }
}
