// buy page
.buy-grid-header {
    .menuAction{
        position: initial;
        margin: auto 10px;
    }

    .navFullContainer {
        height: 100vh;

        .dropMenu {
            right: unset;
            left: 15px;

            .connect-button-wrapper {
                margin: 10px auto;
                transform-origin: unset;
            }
        }
    }
}

// learn page
.titleContainer {
    .menuAction{
        position: initial;
        margin: auto 10px;
    }

    .navFullContainer {
        height: 100vh;

        .dropMenu {
            right: unset;
            left: 15px;

            .connect-button-wrapper {
                margin: 10px auto;
                transform-origin: unset;
            }
        }
    }

    .navBar {
        .connectButtonWrapper {
            transform-origin: unset;
        }
    }
}

// initial page
.navFullContainer {
    display: flex;
    background-color: #0000006b;
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 9999999;
}

.menuAction {
    width: 40px;
    height: 40px;
    background-color: white;
    z-index: 10000000 !important;
    position: absolute;
    right: 20px;
    top: 37px;
    border-radius: 25px;
    overflow: hidden;

    img {
        width: 100%;
    }

    .menuDots {
        color: black;
        font-weight: 900;
        font-size: 26px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: -6px;
    }
}

.dropMenu {
    z-index: 1000000 !important;
    background-color: white;
    border-radius: 12px;
    height: fit-content;
    width: 175px;
    display: flex;
    padding: 10px;
    flex-direction: column;
    position: absolute;
    right: 20px;
    top: 80px;

    .connect-button-wrapper {
        margin-bottom: 10px;
    }

    a:hover {
        text-decoration: none;
        color: white !important;
    }

    .largeItem {
        color: black;
        font-weight: 800;
        font-size: 30px;
        padding: 5px;
        border-radius: 10px;
        text-align: center;
        transition: 0.4s;
        background: linear-gradient(45deg, var(--c1, #fff), var(--c2, #fff) 51%, var(--c1, #65f6a6)) var(--x, 0)/ 300%;
    }

    .largeItem:hover {
        --x: 100%;
    }

    .buy:hover {
        --c1: #f67465;
        --c2: #e3f665;
    }
    
    .play:hover {
        --c1: #9a65f6;
        --c2: #f665dc;
    }

    .learn:hover {
        --c1: #65f6a6;
        --c2: #6598f6;
    }

    .smallItem {
        transition: 0.4s;
        margin-right: unset !important;
    }

    .navDropItem {
        
    }
    
    .iconContainer {
        margin-top: 5px;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;

        a:hover {
            color: black !important;
        }
    }
}

// player page
.nft-jacker.jacked {

    .menuAction {
        right: 25px;
        bottom: 25px;
        top: unset;
    }

    .dropMenu {
        right: 20px;
        bottom: 75px;
        top: unset;
    }
}

// mobile
@media screen and (max-width: 600px) {

    // initial page mobile
    .menuAction {
        bottom: 15px;
        width: 35px;
        height: 35px;
        top: unset;
        right: 10px;

        .menuDots {
            line-height: 35px;
            font-size: 16pt;
        }
    }

    .dropMenu {
        right: 10px;
        bottom: 55px;
        top: unset;
    }

    // player page mobile
    .nft-jacker.jacked {
        .menuAction {
            bottom: 10px;
            width: 35px;
            height: 35px;
    
            img {
                width: 21px;
                margin-top: 7px;
                margin-left: 7px;
                background-color: transparent;
            }
    
            .menuDots {
                line-height: 35px;
                font-size: 16pt;
            }
        }
    }

    // buy page mobile
    .buy-grid-header {
    
        .navFullContainer {
    
            .dropMenu {
                top: 75px;
            }
        }
    }

    // learn page mobile
    .titleContainer {
        .navFullContainer {
    
            .dropMenu {
                top: 65px;
            }
        }
    }
}
