.remix-list-container {
    position: absolute;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: black;
    z-index: 999999;
    left: 0;
    right: 0;
    height: 500px;
    align-items: center;
    padding: 20px;
    height: fit-content;
    top: 100vh;

    .remix-close-button-container {
        display: flex;
        justify-content: flex-end;
        width: 80%;
        position: fixed;
    }

    .no-remix-div {
        color: white;
        text-align: center;
        padding: 20px;
    }

    .remix-list-item-container.playing{
        background-color: white;
        color: black;

        .remix-address {
            .ens-name {
                color: black !important;
            }
        }
    }
    
    .remix-list-item-container{
        cursor: pointer;
        display: flex;
        flex-direction: column;
        color: white;
        align-items: center;
        margin-bottom: 10px;
        padding: 20px;
        border-radius: 15px;

        .remix-icon {
            height: 60px;
            width: 60px;
            border-radius: 35px;
            filter: brightness(1.5);
        }

        .remix-info-container {
            display: flex;
            flex-direction: column;
            align-items: center;

            .remix-number {
                font-weight: 700;
                font-size: 18px;
            }

            .remix-created-on {
                font-size: 13px;
            }

            .remix-address {
                font-size: 14px;

                .ens-name {
                    color: white;
                }
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .remix-list-container {
        top: unset;
    }
}
