.nft-jacker.jacked {
    .playlist-container {
        background-color: rgb(0 0 0 / 82%);
        .playlist-trigger {
            flex-direction: row;
            display: flex;
            img {
                flex: 1;
                height: unset;
                width: 100px;

            }

            .playlist-name {
                text-align: left;
                margin-left: 10px;
                border-bottom: 1px solid white;
                font-size: 18pt;
                position: unset;
                height: unset;
                margin-top: unset;
            }
        }
    }
    .playlist-item {
        p {
            font-size: 10pt;
        }
    }
}


.playlist-item.current {
    background-color: white;
    color: black;
    border-radius: 15px
}

.playlist-trigger {
    .playlist-name {
        width: 100%;
        text-align: center;
        font-size: 25pt;
        position: absolute;
        height: fit-content;
        margin-top: 55px;
    }
}
