.next-gen {
    .enter-overlay.hide {
        opacity: 0;
        animation-name: hide-enter-overlay;
        animation-duration: 4s;
        
        img, .click-text {
            display: none;
        }
    }
    .enter-overlay {
        opacity: 1;
        position: absolute;
        height: 100%;
        width: 100%;
        z-index: 1000000;
        background-color: black;
        display: flex;

        img:hover {
            animation-name: hover-logo-future;
            animation-duration: 2s;
            filter: brightness(1.5);
        }
        .inner-overlay {
            margin: auto;
            cursor: pointer;

            .click-text {
                margin-top: 20px;
                text-align: center;
            }
        }
        img {
            animation-name: unhover-logo-future;
            animation-duration: 2s;
            filter: brightness(1);
            cursor: pointer;
            width: 30vh;
        }
    }
    .drop.nft-jacker.jacked.thugger  {
        padding-bottom: unset;
        .drop-description {
            display: none;
        }
        .spore-embed-container {
            background-color: red;
            padding: unset !important;

            iframe {
                border-radius: 0px !important;
            }

            .spore-embed-helper {
                z-index: 1000;
                .song-title {
                    color: black;
                }
            }
            .shuffle-buttons {
                top: unset;
                bottom: 50px;
                z-index: 10000;
                height: 60px;

                .song-waveform {
                    top: 10px;
                }
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .next-gen {
        .drop.thugger.nft-jacker {
            .flex.w-full {
                margin-top: unset;
            }

            .spore-embed-helper {
                top: 50px;
                left: 30px;
            }
            .shuffle-buttons {
                top: unset !important;
                bottom: 30px;
            }
        }
    }
}

@keyframes hide-enter-overlay {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes hover-logo-future {
    from {
        filter: brightness(1);
    }
    to {
        filter: brightness(1.5);
    }
}
@keyframes unhover-logo-future {
    from {
        filter: brightness(1.5);
    }
    to {
        filter: brightness(1);
    }
}


