.infoBox {
    height: fit-content;
    width: 375px;
    background: rgba(41, 41, 41, 0.971);
    position: absolute;
    bottom: 0px;
    left: 0px;
    top: 0px;
    right: 0px;
    margin: auto;
    border-radius: 20px;
    padding: 25px;
    color: white;
    display: flex;
    flex-direction: column;
    text-align: center;

    .go-to-landing {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
    }
    
    img.spores-logo-black {
        width: 30px;
        height: 30px;
    }

    p {
        margin-top: 10px;
    }

    .join-waitlist-container {
        margin-top: 10px;
        margin-left: auto;
        margin-right: auto;
    }
}

.infoContainer {
    display: flex;
    background-color: #0000006b;
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 1000000;
}

.buyLink {
    text-decoration: underline;
    font-weight: 600;
    color: #61fc9f;
}

.buyLink:hover {
    color: adjust-hue($color: #61fc9f, $degrees: 45);
}

.learnLink {
    text-decoration: underline;
    font-weight: 600;
    color: #6197fc;
}

.learnLink:hover {
    color: adjust-hue($color: #6197fc, $degrees: 10);
}

.timer {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    z-index: 1;
}

.mintButton {
    background-color: black;
    margin-top: 40px;
    padding: 15px;
    margin-left: 15px;
    margin-right: 15px;
    border-radius: 5px;
    font-weight: 700;
    text-align: center;

    span {
        background: -webkit-linear-gradient(60deg, red 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
        background: -moz-linear-gradient(60deg, red 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
        background: linear-gradient(60deg, red 0%, #ffe500 40%, #00f7ff 60%, blue 80%, #ff00a1 90%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        display: inline-block;
        -webkit-mask:linear-gradient(-60deg,#000 30%,#0005,#000 70%) right/300% 100%;
        background-repeat: no-repeat;
        padding-left: 8px;
        padding-right: 8px;
        animation: shimmer 2.7s infinite;
    }
}
